import SearchIcon from '@mui/icons-material/Search'
import {
  Box,
  Container,
  FormControl,
  Grid,
  IconButton,
  InputAdornment,
  InputLabel,
  MenuItem,
  OutlinedInput,
  Pagination,
  Select,
  Stack,
  Typography,
  useMediaQuery,
  useTheme
} from '@mui/material'
import axios from 'axios'
import { useSnackbar } from 'notistack'
import { useEffect, useState } from 'react'
import { useAuthHeader } from 'react-auth-kit'
import { Helmet } from 'react-helmet-async'
import { useSearchParams } from 'react-router-dom'
import ClubList from 'src/components/ClubList'
import PageTitleWrapper from 'src/components/PageTitleWrapper'
import categories from 'src/data/categories'
import types from 'src/data/types'

interface SearchAPIResponse {
  clubs: {
    id: number
    name: string
    type: string
    category: string
  }[]
  numPages: number
}

function Search() {
  const [searchParams, setSearchParams] = useSearchParams()
  const [result, setResult] = useState(null)
  const authHeader = useAuthHeader()
  const theme = useTheme()
  const matches = useMediaQuery(theme.breakpoints.up('sm'))
  const [params, setParams] = useState({
    name: searchParams.get('name') ?? '',
    category: searchParams.get('category') ?? '',
    type: searchParams.get('type') ?? '',
    page: searchParams.has('page') ? parseInt(searchParams.get('page')) : null
  })
  const { enqueueSnackbar } = useSnackbar()

  const handleSubmit = event => {
    event.preventDefault()
    const search = document.getElementById('search') as HTMLInputElement
    setParams({ ...params, name: search.value, page: null })
  }

  useEffect(() => {
    const nextInit = Object.assign(
      {},
      params.name ? { name: params.name } : null,
      params.category ? { category: params.category } : null,
      params.type ? { type: params.type } : null,
      params.page ? { page: params.page } : null
    )
    setSearchParams(nextInit)
  }, [params])

  useEffect(() => {
    for (const [key, value] of searchParams) {
      searchParams.set(key, encodeURIComponent(value))
    }
    axios
      .get<SearchAPIResponse>(
        `${process.env.REACT_APP_APIHOST}/search/clubs?${searchParams.toString()}&count=${encodeURIComponent(
          matches ? 20 : 10
        )}`,
        {
          headers: { Authorization: authHeader() }
        }
      )
      .then(res => {
        if (res.data.clubs.length > 0) setResult(res.data)
        else enqueueSnackbar('주어진 조건에 해당하는 동아리가 없어요 T.T', { variant: 'warning' })
      })
      .catch(err =>
        enqueueSnackbar(err.response.data?.message ?? '서버에서 오류가 발생한 것 같아요. 다시 시도해주세요', {
          variant: 'error'
        })
      )
  }, [searchParams])

  return (
    <>
      <Helmet>
        <title>검색 결과</title>
      </Helmet>
      <PageTitleWrapper>
        <Grid container alignItems="center">
          <Grid item>
            <Typography variant="h3" component="h3" gutterBottom>
              검색 결과
            </Typography>
            <Typography variant="subtitle2">키워드와 카테고리, 분류를 활용해 원하는 동아리를 찾아보세요</Typography>
          </Grid>
        </Grid>
        <Box component="form" onSubmit={handleSubmit}>
          <FormControl variant="outlined" fullWidth sx={{ marginTop: 2, backgroundColor: 'white' }}>
            <OutlinedInput
              id="search"
              type="search"
              placeholder="동아리 이름을 검색해보세요"
              defaultValue={params.name}
              endAdornment={
                <InputAdornment position="end">
                  <IconButton aria-label="Search clubs" type="submit" edge="end">
                    <SearchIcon />
                  </IconButton>
                </InputAdornment>
              }
            />
          </FormControl>
        </Box>
      </PageTitleWrapper>
      <Container
        sx={{ display: 'flex', flexDirection: 'column', justifyContent: 'space-between', alignItems: 'center' }}
      >
        <Stack
          width="100%"
          direction={matches ? 'row' : 'column'}
          spacing={1}
          justifyContent={'flex-end'}
          sx={{ marginBottom: 3 }}
        >
          <FormControl size="small" fullWidth={!matches}>
            <InputLabel id="category-label">카테고리</InputLabel>
            <Select
              labelId="category-label"
              id="category"
              label="카테고리"
              value={params.category}
              onChange={e => setParams({ ...params, category: e.target.value, page: null })}
              sx={{ minWidth: '180px' }}
            >
              <MenuItem value="">선택하지 않음</MenuItem>
              {categories &&
                Object.keys(categories).map(k => (
                  <MenuItem key={k} value={k}>
                    {categories[k]}
                  </MenuItem>
                ))}
            </Select>
          </FormControl>
          <FormControl size="small" fullWidth={!matches}>
            <InputLabel id="type-label">분류</InputLabel>
            <Select
              labelId="type-label"
              id="type"
              label="분류"
              value={params.type}
              onChange={e => setParams({ ...params, type: e.target.value, page: null })}
              sx={{ minWidth: '180px' }}
            >
              <MenuItem value="">선택하지 않음</MenuItem>
              {types &&
                Object.keys(types).map(k => (
                  <MenuItem key={k} value={k}>
                    {types[k]}
                  </MenuItem>
                ))}
            </Select>
          </FormControl>
        </Stack>
        <ClubList clubs={result?.clubs} />
        <Pagination
          page={params.page ?? 1}
          count={result?.numPages ?? 1}
          size={matches ? 'medium' : 'small'}
          onChange={(e, v) => setParams({ ...params, page: v })}
          sx={{ my: 3 }}
        />
      </Container>
    </>
  )
}

export default Search
