/* eslint-disable @typescript-eslint/no-unused-vars */
import { Box, Card, CardActionArea, CardContent, Chip, Stack, Typography, useMediaQuery, useTheme } from '@mui/material'
import categories from 'src/data/categories'
import types from 'src/data/types'

export interface ClubPreviewProps {
  clubId: number
  name: string
  category: string
  type: string
}

const randomColor = () => {
  const pastelColors = [
    'rgba(254, 163, 170, 0.3)',
    'rgba(248, 184, 139, 0.3)',
    'rgba(250, 248, 13, 0.3)',
    'rgba(186, 237, 145, 0.3)',
    'rgba(178, 206, 254, 0.3)',
    'rgba(242, 162, 232, 0.3)'
  ]
  const colorId = Math.floor(Math.random() * pastelColors.length)
  return pastelColors[colorId]
}

function ClubPreview({ clubId, name, category, type }: ClubPreviewProps) {
  const theme = useTheme()
  const matches = useMediaQuery(theme.breakpoints.up('sm'))

  return (
    <Card>
      <CardActionArea href={`/club/${clubId}`} sx={{ display: 'flex', flexDirection: matches ? 'column' : 'row' }}>
        <Box width={matches ? '100%' : '90px'} height="90px" sx={{ backgroundColor: randomColor() }} />
        <CardContent sx={{ flex: 1 }}>
          <Typography gutterBottom variant="h5" component="div">
            {name}
          </Typography>
          {matches && (
            <Typography variant="body2" color="text.secondary">
              Lorem Ipsum is simply dummy text of the printing and typesetting industry
            </Typography>
          )}
          <Stack direction="row" justifyContent="flex-start" spacing={1} sx={{ marginTop: theme.spacing(1) }}>
            <Chip label={types[type]} size="small" sx={{ padding: theme.spacing(0.5), fontSize: matches ? 13 : 11 }} />
            <Chip
              label={categories[category]}
              size="small"
              sx={{ padding: theme.spacing(0.5), fontSize: matches ? 13 : 11 }}
            />
          </Stack>
        </CardContent>
      </CardActionArea>
    </Card>
  )
}

export default ClubPreview
